import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { ActivatedRoute } from '@angular/router';
import { RequestService } from 'src/app/api/request.service';
import { UtilService } from 'src/app/services/util.service';
import { DialogService } from 'src/app/api/util/dialog.service';

@Component({
  selector: 'app-new-challenge',
  templateUrl: './new-challenge.page.html',
  styleUrls: ['./new-challenge.page.scss'],
})
export class NewChallengePage implements OnInit {

  public id : any;
  public gameModel : Model;
  public game : any ;

  constructor(
    public route : ActivatedRoute,
    public requesService : RequestService,
    public util : UtilService,
    public dialogService : DialogService, 

  ) {
    this.id = this.route.snapshot.paramMap.get('id');
    this.gameModel = new Model('Game',requesService);

   }

  async ngOnInit() {
    
    await this.dialogService.loadingProgres();

    
    this.gameModel.api_find(this.id).subscribe(
      response => {
        if(response['status'] == 'success'){
          this.game = response['Game'];
        }
        this.dialogService.dimissLoaging();
      },error => {
        this.dialogService.dimissLoaging();
      }
      ,()=>{
        this.dialogService.dimissLoaging();
      }
    )
  }

  async confirm(){
    await this.dialogService.loadingProgres();

    this.gameModel.api_functionModel(this.id,'confirm').subscribe(
      response => {
        this.game = response['data'];
        this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging();
      },
      () => {
        this.dialogService.dimissLoaging();  
      }
    )

  }

  async cancel(){
    await this.dialogService.loadingProgres();

    this.gameModel.api_functionModel(this.id,'cancel').subscribe(
      response => {
        this.game = response['data'];
        this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging();
      },
      () => {
        this.dialogService.dimissLoaging();  
      }
    )
  }

}
